.hamburger {
  display: inline-block;
  box-sizing: border-box;
  position: relative;
  z-index: 30;
  cursor: pointer;
  width: 50px;
  height: 50px;
}
.hamburger__parts {
  display: inline-block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 45%;
  height: 3px;
  border-radius: 3px;
  background-color: #ffffff;
  box-sizing: border-box;
  transition: all 0.5s;
}
.hamburger__parts:nth-of-type(1) {
  top: 15px;
}
.hamburger__parts:nth-of-type(2) {
  top: 23px;
}
.hamburger__parts:nth-of-type(3) {
  top: 31px;
}
.hamburger.active .hamburger__parts:nth-of-type(1) {
  top: 18px;
  left: 18px;
  transform: translateY(6px) rotate(-45deg);
  width: 30%;
}
.hamburger.active .hamburger__parts:nth-of-type(2) {
  opacity: 0;
}
.hamburger.active .hamburger__parts:nth-of-type(3) {
  top: 30px;
  left: 18px;
  transform: translateY(-6px) rotate(45deg);
  width: 30%;
}

